<template>
  <div>
    <h2>Pridti Darbuotoj</h2>

    <form-item @onSubmit="createUser">
      <!-- Vardas Pavard -->
      <div>
        <input type="text"
               placeholder="Vardas" required
               v-model="newUser.first_name">
        <input type="text"
               placeholder="Pavard" required
               v-model="newUser.last_name">
      </div>
      <!-- Email -->
      <input type="email"
             placeholder="Elektroninis patas"
             class="input-long" required
             v-model="newUser.email">

      <!-- Company -->
      <select class="input-long" required v-model="newUser.company_id">
        <option selected hidden class="placeholder" value="">mon</option>
        <option v-for="item in companyList" :key="item.id" :value="item.id">{{item.name}}</option> <!-- moni sraas -->
      </select>

      <!-- Errors and Submit btn -->
      <div class="button-container">
        <p class="error-msg">{{ errorMsg }}</p>
        <button class="btn" type="submit">Pridti</button>
      </div>

    </form-item>
  </div>
</template>

<script>
  import FormItem from "@/components/FormItem";

  export default {
    name: "AddUser",
    props: [ 'companyList', 'errorMsg', 'company_id' ],
    components: { FormItem },
    data() {
      return {
        newUser: {
          first_name: '',
          last_name: '',
          email: '',
          company_id: '',
          role: 0,
        },
      }
    },
    created() {
      if(this.company_id){
        this.newUser.company_id = this.company_id;
      }
    },
    methods:{
      createUser() {
        this.$emit('createUser', this.newUser);
      },
    }
  }
</script>

<style scoped>

  h2{
    margin-top: .4em;
  }

</style>