<template>
  <div>
    <h2>Pridti rang</h2>

    <form-item @onSubmit="addNewGear">
      <!-- Kodas ir Kiekis -->
      <div>
        <input type="text"
               placeholder="Kodas"
               v-model="newGear.code"
               @change="checkCode"
               required title="Kodas">
        <input type="number"
               step="1" max="50"
               placeholder="Kiekis"
               v-model="newGear.amount"
               required title="Kiekis">
      </div>

      <!-- Pavadinimas -->
      <input type="text"
             placeholder="Pavadinimas"
             class="input-long"
             v-model="newGear.name"
             required title="Pavadinimas">

      <!-- Apraymas -->
      <textarea maxlength="255"
                placeholder="Apraymas"
                class="input-long"
                v-model="newGear.description"
                required title="Apraymas"/>
      <p class="char-size">Liko simboli: {{descriptionCharNum}}</p>

      <!-- Serijos Nr. ir Kaina -->
      <div class="relative-container">
        <input type="text"
               placeholder="Serijos Numeris"
               v-model="newGear.serial_number"
               required title="Serijos numeris"
               :class="{'input-error': errorInput.serial_number}"
               @focus="delete errorInput.serial_number">
        <input type="number" step=".01"
               placeholder="Vieneto Kaina"
               id="unit-price"
               v-model="newGear.unit_price"
               required title="Vieneto kaina">
        <label for="unit-price" id="euro-sign"></label>
      </div>

      <!-- Ilgalaik ar trumpalaik ranga -->
      <div class="btn-container">
        <div>
          <label>
            <input type="radio" name="long-term" value="1" v-model="newGear.long_term">
            Ilgalaik ranga
          </label>
          <label>
            <input type="radio" name="long-term" value="0" v-model="newGear.long_term">
            Trumpalaik ranga
          </label>
        </div>

        <!-- Submit btn -->
        <button class="btn">Pridti</button>
      </div>

      <!-- Errors -->
      <p v-for="(message, index) in errorInput" :key="index" class="error-msg">{{ message }}</p>

    </form-item>
  </div>
</template>

<script>
  import DataMixin from "@/components/mixins/DataMixin";
  import FormItem from "@/components/FormItem";

  export default {
    name: "AddItem",
    mixins: [ DataMixin ],
    props: [ 'user' ],
    components: { FormItem },
    data() {
      return {
        errorInput: {},
        newGear: {
          description:'',
          name: '',
          code:'',
          serial_number: '',
          amount: '',
          unit_price: '',
          long_term: 1,
          user_id: this.user,
        },
      }
    },
    computed: {
      descriptionCharNum() {
        return 255 - this.newGear.description.length;
      }
    },
    methods: {
      addNewGear() {
        this.postData(
            this.$store.getters.API_baseURL + '/gear',
            this.newGear,
            this.addGearSuccess,
            this.addGearError
        )
      },

      addGearSuccess() {
        this.$emit('success');
        this.errorInput = {};
      },

      addGearError(error) {
        this.errorInput = {};
        if (error.response.data.error){
          if(error.response.data.error.serial_number) {
            this.errorInput.serial_number = "Inventorius iuo serijos numeriu jau pridtas.";
          }
        }
        if (error.response.data.message === "Gear does not match with other ones with the same code") {
          this.errorInput.code = "Duomenys nesutampa su kitu io kodo inventoriumi.";
        }
      },

      checkCode(){
        this.$http.get(this.$store.getters.API_baseURL + "/code/" + this.newGear.code, this.config)
            .then(response => {
               let { name, unit_price, description, long_term } = response.data;
               this.newGear = {...this.newGear, name, unit_price, description, long_term };
            }).catch(() => {})
      }
    }
  }
</script>

<style scoped>

  h2 {
    margin-top: 0;
  }

  textarea {
    height: 44px;
    transition: height 400ms;
    resize: vertical;
  }

  textarea:focus {
    height: 90px;
  }

  textarea {
    margin-bottom: 0;
  }

  .char-size {
    text-align: right;
    font-size: .75em;
    color: var(--clr-grey);
    margin: 0 0 1rem;
  }

  .relative-container {
    position: relative;
  }

  .error-msg {
    margin-top: 1em;
  }

  #euro-sign {
    position: absolute;
    top: .7em;
    right: .5em;
    color: grey;
    width: fit-content;
  }

  @media (max-width: 580px) {
    #euro-sign {
      top: calc(.7em + 4.7em);
    }
  }

  .btn-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  .btn-container label:not(:first-child) {
    margin-top: .9em;
  }

  .btn-container div{
    color: grey;
    display: flex;
    flex-direction: column;
  }

  .btn-container input{
    width: fit-content;
    margin: 0 1em 0 0;
  }

  /* remove input number spinner button */
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  input[type=number] {
    -moz-appearance: textfield;
  }

</style>